import React from 'react';
import './App.css';
import './index.css';
import './style/reset.css';
import './style/animation.css';
import './style/header+footer.css';

import Header from './Components/Header';
import Footer from './Components/Footer';
import Start from './Components/Start';
import Qna from './Components/Qna';
import Wait from './Components/Wait';
import Result from './Components/Result';


const App = () => {
  const [stage, setStage] = React.useState(2);
  const handleStage = () => {
    setStage(stage + 1);
    if (stage === 3) {
      showHeaderFooter();
    }
  }

  const header = document.getElementById('header');
  const footer = document.getElementById('footer');

  const [darkModeFlag, setDarkModeFlag] = React.useState(false);

  const head = document.getElementsByTagName('head')[0];
  const dark_css = document.createElement('link');
  dark_css["rel"] = 'stylesheet';
  dark_css["type"] = 'text/css';
  dark_css["href"] = 'style/darkmode.css';

  const goDark = () => {
    setDarkModeFlag(true);
    head.appendChild(dark_css);
  }
  const goLight = () => {
    setDarkModeFlag(false);
    if (head.lastChild.rel === 'stylesheet')
      head.removeChild(head.lastChild);
  }


  const handleDarkMode = () =>
    (window.matchMedia &&
      window.matchMedia('(prefers-color-scheme: dark)').matches)
      ? goDark() : goLight();
  const switchMode = () => darkModeFlag ? goLight() : goDark();
  
  React.useEffect(() => handleDarkMode(), []);


  const showHeaderFooter = () => {
    setTimeout(() => {
      header.current.style.display = 'block';
      footer.current.style.display = 'block';
      header.current.style.animation =
        'fade-in 0.3s forwards';
      footer.current.style.animation =
        'fade-in 0.3s forwards';
    }, 600);
  }

  const switchComponent = num => {
    switch (num) {
      // 필요한 props 내려보내기
      case 0: return <Start />;
      case 1: return <Qna />;
      case 2: return <Wait handleStage={handleStage}/>;
      /* 
        test를 위해 Result에 name과 score를 임의로 넣음
        state로 관리되어야 함
      */
      case 3: return <Result name='rey' score={51} />;
      default: return (
        <>
          Something wrong...
        </>
      )
    }
  };


  return (
    <>
      <Header 
        handleMode={switchMode} 
        flag={darkModeFlag} 
      />
      {switchComponent(stage)}
      <Footer />
    </>
  );
}

export default App;
